<template>
    <el-container class="main-container">
      <!-- 头部区域 -->
      <el-header>
        <!-- 左侧的 logo -->
        <img src="../../assets/images/logo.png" alt="" width=200px />
        <!-- 右侧的菜单 -->
        <el-menu
          class="el-menu-top"
          mode="horizontal"
          background-color="#23262E"
          text-color="#fff"
          active-text-color="#409EFF"
        >
          <el-submenu index="1">
            <template slot="title">
              <!-- 头像 -->
              <img src="../../assets/logo.png" alt="" class="avatar" />
              <span>个人中心</span>
            </template>
            <el-menu
    :default-active="$route.path"
    class="el-menu-vertical-demo"
    background-color="#23262E"
    text-color="#fff"
    active-text-color="#409EFF"
    unique-opened
    router
   >
            <template v-for="item in menus" >
            <!-- 不包含子菜单的“一级菜单” -->
            <template v-if="item.title==='个人中心'">

              <el-menu-item :index="subItem.indexPath" v-for="subItem in item.children" :key="subItem.indexPath"   >
                <i :class="subItem.icon"></i>{{ subItem.title }}
              </el-menu-item>

          </template>
          </template>
</el-menu>
          </el-submenu>
          <el-menu-item index="2" @click="logoutFn"><i class="el-icon-switch-button"></i>退出</el-menu-item>
        </el-menu>
      </el-header>

      <!-- 面包屑 -->
<template>
  <div id="app" class="grid-content ">
    <el-tag

      v-for="(item,index) in tags12"
      :key="item.indexPath"
      :closable="item.title !== '首页'"
      :effect="$route.meta.title === item.title ? 'dark':'plain'"
      @click = 'clickTags(item)'
      @close = 'handleTag(item,index)'
      size="medium "
    >
      {{ item.title }}
    </el-tag></div>
</template>
      <el-container>
        <!-- 侧边栏区域 -->
        <el-aside width="200px">
        <div class="user-box">
          <img :src="user_pic" alt="" v-if="user_pic" />
          <img src="../../assets/images/head.png" alt="" v-else />
          <span>欢迎 {{ nickname || username }}</span>
        </div>
        <!-- 左侧导航菜单 -->
        <el-menu
    :default-active="$route.path"
    class="el-menu-vertical-demo"
    background-color="#23262E"
    text-color="#fff"
    active-text-color="#409EFF"
    unique-opened
    router
   >
        <template v-for="item in menus">
            <!-- 不包含子菜单的“一级菜单” -->
            <el-menu-item @click='toMenu(item)' :index="item.indexPath" :key="item.indexPath" v-if="!item.children">
              <i :class="item.icon"></i>{{ item.title }}
            </el-menu-item>

            <!-- 包含子菜单的“一级菜单” -->
            <el-submenu :index="item.indexPath" :key="item.indexPath+1" v-else>
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{ item.title }}</span>
              </template>
              <!-- 循环渲染“二级菜单” -->
              <el-menu-item @click="toMenu(subItem)" :index="subItem.indexPath" v-for="subItem in item.children" :key="subItem.indexPath">
                <i :class="subItem.icon"></i>{{ subItem.title }}
              </el-menu-item>
            </el-submenu>
          </template>
        </el-menu>
      </el-aside>
        <el-container>
          <!-- 页面主体区域 -->
          <el-main>
    <router-view ></router-view>
</el-main>
          <!-- 底部 footer 区域 -->
          <el-footer>  <el-link href="https://rady3.github.io/" target="_blank">© https://rady3.github.io/ - LQY
            <i class="el-icon-view el-icon--right"></i>
          </el-link>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>

  </template>

<script>
import { mapGetters, mapState, mapMutations } from 'vuex'
import { getMenusAPI } from '@/api'
export default {
  name: 'my-layout',
  data () {
    return {
      menus: [] // 侧边栏数据
    }
  },
  created () {
    this.getMenusListFn()
  },
  computed: {
    tags12 () {
      return this.$store.state.tabList
    },
    ...mapGetters(['nickname', 'username', 'user_pic']),

    ...mapState({
      tags: state => state.tabList
    })

  },
  methods: {
    // ...其他
    // 获取侧边栏菜单数据
    async getMenusListFn () {
      const { data: res } = await getMenusAPI()
      console.log('res.data', res.data)
      this.menus = res.data
    },
    toMenu (item) {
      this.$store.commit('SELECTMENU', item)
    },
    logoutFn () {
      // 询问用户是否退出登录
      this.$confirm('您确认退出登录吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // TODO：执行退出登录的操作
          // 1. 清空 token
          this.$store.commit('updateToken', '')
          this.$store.commit('updateUserInfo', '')
          // 2. 跳转到登录页面
          this.$router.push('/login')
        })
        .catch((err) => err)
    },
    // 调用store中的mutation
    ...mapMutations(['closeTag']),
    // 点击TAG跳转路由
    clickTags (item) {
      this.$router.push(item.indexPath)
    },
    // 点击删除按钮，删除TAG
    handleTag (item, index) {
      // 调用mutation中的CLOSETAG事件

      this.closeTag(index)
      // 删除后的tags的长度
      const length = this.tags12.length

      // 点击删除的tag是最后一个
      if (index === length) {
        this.$router.push(this.$store.state.tabList[length - 1].indexPath)
      }
    },
    // 删除指定tag标签
    closeTag (index, val) {
      this.$store.state.tabList.splice(index, 1)
    }

  }
}
</script>

  <style lang="less" scoped>

  @import url("//unpkg.com/element-ui@2.15.13/lib/theme-chalk/index.css");
  // 左侧边栏用户信息区域
.user-box {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  user-select: none;
  img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 15px;
    object-fit: cover;
  }
  span {
    color: white;
    font-size: 12px;
  }
}
  .main-container {
    height: 100%;
    .el-header,
    .el-aside {
      background-color: #23262e;

    }
    .el-header {
      padding: 0;
      display: flex;
      position: relative;
      justify-content: space-between;
    }
    .el-main {
      overflow-y: scroll;
      height: 0;
      background-color: #F2F2F2;
    }
    .el-footer {
      background-color: #eee;
      font-size: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .avatar {
    border-radius: 50%;
    width: 35px;
    height: 35px;
    background-color: #fff;
    margin-right: 10px;
    object-fit: cover;
  }
  .el-aside {
  .el-submenu,
  .el-menu-item {
    width: 200px;
    user-select: none;
  }
}
.el-aside {
  margin-top: -30px;

  position: relative;

}
.grid-content {
    border-radius: 4px;

    font-size: large;
    display: flex;
    margin-left: 200px;

    align-items: center;
    min-height: 30px;
  }
  .bg-purple-light {
    background: #F2F2F2;
  }

  .bg-purple-light {
    background: #F2F2F2;
  }
.wrapper {

  /*height: 100vh;
  background-image: linear-gradient(to bottom right, #FC466B, #3F5EF8);
  overflow: hidden;*/
  /*背景图*/
  background: url("../../assets/images/login_bg.jpg");
  width: 100%;
  height: 100%;
  position: fixed;
  background-size: 100% 100%;

}

  </style>
